import { useState, useEffect, useRef } from "react";
import "./login.css";
import { login } from "../../api/post";
import Input from "../../components/atoms/input";
import Button from "../../components/atoms/button";
import { useDispatch } from "react-redux";
import { SetToken } from "../../store/Reducers/Auth";
import { useSelector } from "react-redux";
import { RootState } from "../../store/store";
import { useHistory } from "react-router";
import Circle from "../../components/atoms/circle";
import { toast } from "react-toastify";
import CircularProgress from "@mui/material/CircularProgress";

const Login: any = () => {
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [flag, setFlag] = useState(false);
  const token = useSelector((state: RootState) => {
    return state.isAuth.isAuth;
  });

  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      history.push("/");
    }
  }, [token]);

  // useEffect(() => {
  //   // ref.addEventListener("keypress", onKeyPress);
  //   const all_input = document.getElementsByTagName("input");
  //   all_input[1].onkeyup = (e) => {
  //     var keyCode = e.key;
  //     if (keyCode == "Enter") {
  //       clickHandler();
  //       return false;
  //     }
  //   };
  //   // return () => {
  //   //   console.log("removing eventlistner");
  //   //   window.removeEventListener("keypress", clickHandler);
  //   // };
  // }, []);

  const clickHandler: any = async () => {
    if (email == "") {
      toast.warning("Please Fill Your Email");
      return;
    }
    if (password == "") {
      toast.warning("Please Fill Your Password");
      return;
    }
    setFlag(true);
    const data = await login(email, password);
    if (Array.isArray(data)) {
      console.log(data[0].Token);
      localStorage.setItem("token", data[0].Token);
      dispatch(SetToken(data[0].Token));
      setFlag(false);
      return;
    } else {
      toast.error("Invalid Credentials");
      setFlag(false);
      return;
    }
  };

  return (
    <div className="login-MainContainer">
      <div
        className="login-Container"
        style={{
          minHeight: "700px",
        }}
      >
        <div className="login-circle">
          <div className="login-circle-dark">
            <Circle color="dark" size="large" />
          </div>
          <div className="login-circle-light">
            <Circle color="light" size="large" />
          </div>
        </div>
        <div className="login-Header">
          <div className="login-text-1">Login</div>
          <div className="login-text-2">Welcome back !</div>
        </div>
        <div className="Login-email">
          <div style={{ width: "80%" }}>
            <Input
              type="email"
              placeholder="pandey27nilesh@gmail.com"
              label="Email"
              value={email}
              setvalue={setEmail}
              height={56}
            />
          </div>
        </div>

        <div className="Login-password">
          <div style={{ width: "80%" }}>
            <Input
              type="password"
              placeholder=""
              label="Password"
              value={password}
              setvalue={setPassword}
              height={56}
            />
          </div>
        </div>

        <div className="Login-button"></div>

        <Button
          title={
            flag ? (
              <CircularProgress
                color="inherit"
                style={{ color: "white" }}
                size={28}
              />
            ) : (
              "Login"
            )
          }
          type="secondary"
          clickHandler={clickHandler}
          padding="24px 170px"
        ></Button>
      </div>
    </div>
  );
};

export default Login;
